body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /* overflow:hidden; */
}

img {
  float: left;
  margin-right: 15px;
}


#splash {
  animation: hideMe 0.5s 1;
  animation-fill-mode: forwards;
}

#App {
  /* Photo by Nadine Primeau on Unsplash */
  background-image: url(https://i.ibb.co/XW03Rgs/nadine-primeau-b-Lk-T8w-GV0-I-unsplash.jpg);
  background-size: cover;
  animation: showMe 0.5s 1;
  animation-fill-mode: forwards;
  /* animation-delay: 0.5s; */
}

#Apptitle {
  height: 120px;
  margin-left: 80px;
  margin-top: -50px;
}

