#bars {
  height: 30px;
  left: 35%;
  margin: -10px 0 0 -20px;
  position: absolute;
  top: 50%;
  width: 40px;
  animation: move 4s infinite;
}

.bar {
  /* background: rgb(218, 210, 214); */
  background: green;
  bottom: 1px;
  height: 10px;
  position: absolute;
  width: 5px;
  animation: sound 0ms -800ms linear infinite alternate;
}

@keyframes sound {
  0% {
    opacity: .35;
    height: 3px;
  }

  100% {
    opacity: 1;
    height: 60px;
  }
}

.bar:nth-child(1) {
  left: 1px;
  animation-duration: 474ms;
}

.bar:nth-child(2) {
  left: 5px;
  animation-duration: 433ms;
}

.bar:nth-child(3) {
  left: 9px;
  animation-duration: 407ms;
}

.bar:nth-child(4) {
  left: 13px;
  animation-duration: 458ms;
}

.bar:nth-child(5) {
  left: 17px;
  animation-duration: 400ms;
}

.bar:nth-child(6) {
  left: 21px;
  animation-duration: 427ms;
}

.bar:nth-child(7) {
  left: 25px;
  animation-duration: 441ms;
}

.bar:nth-child(8) {
  left: 29px;
  animation-duration: 419ms;
}

.bar:nth-child(9) {
  left: 33px;
  animation-duration: 487ms;
}

.bar:nth-child(10) {
  left: 36px;
  animation-duration: 474ms;
}

.bar:nth-child(11) {
  left: 45px;
  animation-duration: 433ms;
}

.bar:nth-child(12) {
  left: 49px;
  animation-duration: 407ms;
}

.bar:nth-child(13) {
  left: 55px;
  animation-duration: 458ms;
}

.bar:nth-child(14) {
  left: 67px;
  animation-duration: 400ms;
}

.bar:nth-child(15) {
  left: 71px;
  animation-duration: 427ms;
}



@keyframes hideMe {
  0% {
    opacity: 0.5;
  }

  25% {
    opacity: 1;
  }

  75% {
    opacity: 0.5;
  }

  100% {
    opacity: 0;
  }
}


@keyframes showMe {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0;
  }

  75% {
    opacity: 0.7;
  }

  100% {
    opacity: 1;
  }
}

