  display: flex;
  flex-direction: column;
  font-family: 'helvetica', sans-serif; }
  #message_window .disconnected {
    background: #FFFFCC;
    padding: 0.25rem; }
  #message_window.connected .disconnected {
    display: none; }
  #message_window .offline {
    display: none; }
  #message_window.offline .disconnected {
    display: none; }
  #message_window.offline .offline {
    background: #FF0000;
    color: #FFF;
    padding: 0.25rem;
    display: block; }
  #message_window .powered_by {
    flex-shrink: 0;
    text-align: center;
    border-bottom: 1px solid #CCC;
    font-size: 14px;
    padding: 0.25rem;
    color: #666; }
    #message_window .powered_by a {
      text-decoration: none;
      color: #666; }
    #message_window .powered_by img {
      position: relative;
      top: 2px; }
  #message_window section {
    flex-grow: 1;
    flex-direction: column-reverse;
    display: flex;
    overflow-y: auto; }
    #message_window section div div {
      margin: 0.25rem;
      clear: both; }
  #message_window footer {
    border-top: 1px solid #CCC;
    padding: 0.25rem; }
    #message_window footer input[type="text"] {
      flex-grow: 1;
      font-size: 1.25rem;
      outline: none;
      border: none; }
    #message_window footer button {
      width: 50px;
      background: none;
      border: 0;
      cursor: pointer;
      color: blue;
      font-weight: bold; }
  #message_window form {
    display: flex;
    margin: 0;
    padding: 0.25rem; }
  #message_window #message_template {
    display: none; }
  #message_window .message {
    font-size: 14px;
    padding: 0.5rem;
    background: #F0F0F0;
    color: #333;
    border-radius: 5px;
    width: auto;
    display: inline-block;
    max-width: 75%; }
  #message_window .file_attachment {
    background: #F0F0F0;
    color: #333;
    border-radius: 5px;
    display: inline-block;
    max-width: 75%; }
    #message_window .file_attachment img {
      max-width: 100%;
      display: block; }
  #message_window .button_message {
    margin: 1rem 0;
    border-radius: 4px;
    border-color: orange;
    border-style: solid;
    color: orange;
    border-width: 1px;
    padding: 0.25rem 1rem;
    text-decoration: none;
    text-align: center;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.25);
    display: block; }
  #message_window .message p {
    margin-top: 0;
    margin-bottom: 0.5rem; }
  #message_window .message p:last-child {
    margin-bottom: 0; }
  #message_window .message.outgoing {
    float: right;
    background: #a795ef;
    color: white; }
  #message_window #message_replies {
    text-align: center;
    overflow-x: auto;
    flex-shrink: 0;
    -webkit-overflow-scrolling: touch;
    /* Lets it scroll lazy */ }
    #message_window #message_replies ul {
      list-style-type: none;
      margin: 0px auto;
      padding: 0; }
      #message_window #message_replies ul li {
        display: inline-block;
        margin: 0.5rem;
        margin-left: 0; }
    #message_window #message_replies a {
      text-decoration: none;
      display: block;
      border: 1px solid #a795ef;
      color: #a795ef;
      border-radius: 16px;
      padding: 0.25rem 1rem;
      font-size: 14px;
      cursor: pointer; }
      #message_window #message_replies a:hover {
        background: #a795ef;
        color: #FFF; }

/* typing indicator CSS based on code by Joseph Fusco -> https://codepen.io/fusco/pen/XbpaYv */
.typing-indicator {
  display: table;
  margin: 0 auto;
  position: relative; }
  .typing-indicator span {
    height: 5px;
    width: 5px;
    float: left;
    margin: 0 1px;
    background-color: #333;
    display: block;
    border-radius: 50%;
    opacity: 0.4; }
    .typing-indicator span:nth-of-type(1) {
      -webkit-animation: 1s blink infinite 0.3333s;
              animation: 1s blink infinite 0.3333s; }
    .typing-indicator span:nth-of-type(2) {
      -webkit-animation: 1s blink infinite 0.6666s;
              animation: 1s blink infinite 0.6666s; }
    .typing-indicator span:nth-of-type(3) {
      -webkit-animation: 1s blink infinite 0.9999s;
              animation: 1s blink infinite 0.9999s; }

@-webkit-keyframes blink {
  50% {
    opacity: 1; } }

@keyframes blink {
  50% {
    opacity: 1; } }

